    font-size: 12px;
    border-radius: 5px;
    -webkit-opacity: 0.7;
    -khtml-opacity: 0.7;
    -moz-opacity: 0.7;
    opacity: 0.7;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=70);
    filter: alpha(opacity = 70);
  }
}

.note-hint-popover {
  min-width: 100px;
  padding: 2px;

  .popover-content {
    padding: 3px;
    max-height: 150px;
    overflow: auto;

    .note-hint-group .note-hint-item {
      display: block !important;
      padding: 3px;

      &.active, &:hover {
        display: block;
        clear: both;
        font-weight: 400;
        line-height: 1.4;
        color: $white;
        white-space: nowrap;
        text-decoration: none;
        background-color: #428bca;
        outline: 0;
        cursor: pointer;
      }
    }
  }
}